@import 'scss/reset';

body {
  background: #ffcbd8;
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  font-family: 'Libre Barcode 39 Text', cursive;
  font-size: 60px;
  color: white;
}

img {
  position: absolute;
  bottom: 0;
  //display: none;
  width: 100%;
  max-width: 700px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  opacity: 0;
  transition: opacity 2000ms ease;


  &.visible {
    //animation: pulse 250ms linear forwards;
    //transition: opacity 200ms ease;
    opacity: 1;

    &.init {
      opacity: 1;
      animation: none;
    }
  }

  &.easter-egg {
    opacity: 1;
    height:50%;
    width: auto;
  }
}

#cat.easter-egg {
  transform: translateX(-50%) scaleY(-1);
  bottom: inherit;
  top: 0;


}

#ben.easter-egg {

}

button {
  position: absolute;
  top: 60px;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 1000ms ease;


  &.init {
    opacity: 1;
  }
}

@keyframes pulse {
  0% {
    transform: translateX(-50%) scale(1.2);
  }

  70% {
    transform: translateX(-50%) scale(1.05);
  }

  100% {
    transform: translateX(-50%) scale(1);
  }
}
